.main {
  overflow: hidden;
  position: relative;
  flex: 1;
}

.main-wrap {
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  position: absolute;
  width: 100%;
  height: 100%;
}
.main-wrap::after,
.main-wrap::before {
  position: absolute;
  width: 1px;
  height: 1px;
  content: "";
}

.main-header {
  z-index: 2;
  position: relative;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04);
}

.input-blank .input-frame{
  border: 0 !important;
}

.main-wrap::after {
  top: -1px;
}
.main-wrap::before {
  bottom: -1px;
}

.main-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.main-inner {
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
}

.main-inner {
  /* padding-bottom: 100px; */
}

.main-footer {
  margin-top: auto;
  width: 100%;
}

.sticky-menu .accordion-arrow__content,
.sticky-menu .accordion-plus__content {
  transition: none !important;
}
