.list-item {
  display: flex;
  align-items: center;
}

.box {
  background: white;
  border-radius: 6px;
  box-shadow: -10px 10px 40px rgba(0, 0, 0, 0.03);
  margin-bottom: 20px;
}

.box.parallax-window {
  background-color: transparent;
}

.box__body {
  padding: 30px;
}

@media (max-width: 767px) {
  .box {
    margin-left: -15px;
    margin-right: -15px;
    border-radius: 0;
    margin-bottom: 10px;
  }
}

.post-css-test {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 30px;
  color: rgba(var(--color-gradient), 0.1);
}
.item {
  background-color: green;
  height: 50px;
}

.ios-nav-hide .modal-dialog-scrollable {
  min-height: calc(100% - 80px);
  max-height: calc(100% - 80px);
}

.input-rnc-btn:checked ~ .ubg-line-secondary {
  background-color: var(--color-primary) !important;
  border-color: var(--color-primary) !important;
}

.input-rnc-btn:checked ~ .ubg-line-secondary .ubtn-inner {
  color: var(--color-invert) !important;
}

.rnc-btn-wrap {
  display: block;
}

.form-group-rnc .rnc {
  padding: 12px 0;
}

.form-group-rnc .rnc > input:checked ~ .rnc-content {
  color: var(--color-primary);
}

.input-rnc-btn ~ .ubox-size-button-default {
  border-radius: 8px !important;
  padding-left: 8px;
  padding-right: 8px;
}

.counter-number {
  width: 36px;
  text-align: center;
}
.box-accordion {
  border-radius: 8px;
}
.box__header {
  padding: 12px 52px 12px 16px;
  min-height: 48px;
  display: flex;
  align-items: center;
  position: relative;
}
.box__header::after {
  position: absolute;
  width: 24px;
  height: 24px;
  margin: auto;
  background-image: url(../media/icons-color/default/default/chevron-down.svg);
  content: "";
  right: 12px;
  background-repeat: no-repeat;
  background-position: center;
  transition: 0.2s all linear;
}
.box-accordion__body > div::before {
  border-top: 1px dashed var(--color-secondary);
  content: "";
  display: block;
  margin-top: -12px;
  margin-bottom: 12px;
}
.open > .box__header::after {
  transform: rotate(-180deg);
}
.box-accordion__body > div {
  padding: 12px 16px 16px 16px;
  line-height: var(--fz-default-line-height);
}

.open-init [data-content] {
  max-height: none !important;
}

.box__header {
  line-height: var(--fz-default-line-height);
}
.bottom-menu {
  position: relative;
  z-index: 2;
  box-shadow: 0px -2px 4px rgba(0, 0, 0, 0.12);
}
.bottom-menu-item {
  display: block;
  padding: 4px 8px 4px 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 2px solid transparent;
}
.bottom-menu-item.active{
  border-top: 2px solid var(--color-primary);
}
.bottom-menu-item .title {
  font-size: var(--fz-h3);
  color: var(--color-default-2);
}

.bottom-menu-item .ic {
  display: flex;
  align-items: center;
  width: 32px;
  height: 32px;
  justify-content: center;
  margin-bottom: 8px;
}
.bottom-menu-item:not(.active) .ic-active,
.bottom-menu-item.active .ic-normal {
  display: none;
}

.bottom-menu-item.active .title {
  color: var(--color-primary);
}
