/*gtc-css*/
:root {

	--color-transparent: transparent;
	--color-invert: #fff;
	--color-default: #000;
	--color-default-2: #666666;
	--color-azure-light: #e8f1f6;
	--color-azure: #C7262E;
	--color-blue: #C7262E;
	--color-blue-shadow: rgba(113, 169, 169, 0.24);
	--color-grey-1: #9e9e9e;
	--color-grey-2: #c4c4c4;
	--color-grey-3: #e0e0e0;
	--color-grey-4: #F6F6F6;
	--color-grey-5: #f5f7f9;
	--color-red: #c91d1d;
	--color-primary: #C7262E;
	--color-sub-primary: #e8f1f6;
	--color-secondary: #e0e0e0;
	--color-danger: #c91d1d;


	--color-hover-transparent: #f5f5f5;
	--color-hover-invert: #f5f5f5;
	--color-hover-azure-light: #d3e5ee;
	--color-hover-azure: #3054B0;
	--color-hover-red: #ae1919;
	--color-hover-primary: #3054B0;
	--color-hover-sub-primary: #d3e5ee;
	--color-hover-danger: #ae1919;


	--color-text-transparent: default;
	--color-text-invert: default;
	--color-text-azure-light: #C7262E;
	--color-text-azure: #ffffff;
	--color-text-grey-2: default;
	--color-text-grey-3: default;
	--color-text-grey-4: default;
	--color-text-grey-5: default;
	--color-text-red: #ffffff;
	--color-text-primary: #ffffff;
	--color-text-sub-primary: #C7262E;
	--color-text-secondary: default;
	--color-text-danger: #ffffff;


	--color-active-azure-light: #bcd8e6;
	--color-active-azure: #3054B0;
	--color-active-red: #931515;
	--color-active-primary: #3054B0;
	--color-active-sub-primary: #bcd8e6;
	--color-active-danger: #931515;

	--fz-h1: 18px;
	--fz-h1-line-height: 1.1;
	--fz-h2: 16px;
	--fz-h2-line-height: 1.1;
	--fz-h3: 12px;
	--fz-h3-line-height: 1.1;
	--fz-p: 14px;
	--fz-p-line-height: 1.3;
	--fz-default: 14px;
	--fz-default-line-height: 1.4;
}
/*end-gtc-css*/